// -------------------------popular
.popular {
    &__label {
        right: $container-s-unit;
        top: 80px;
    }

    &__img {
        padding-top: 84.7%;
        background-image: url('../images/feature.png');
        background-size: cover;
        background-repeat: no-repeat;
        
        transition: 1s;
        cursor: pointer;

        &:hover {
            background-image: url('../images/feature-hover.png');
        }
    }
}