.half__item {
    @include before(12px, 12px) {
        background-color: #B4B4B4;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: -20px;
        transform: translateY(-50%);
    }

    @include after(200px, 1px) {
        background-color: #B4B4B4;
        position: absolute;
        top: 50%;
        left: -220px;
        transform: translateY(-50%);
    }

}