.products__list {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
}

.products__item {
    width: 25%;
    @extend  .mb-10;

    &__pic {
        display: flex;
        @include size(100%, 160px);

        >* {
            margin: auto;
        }
    }

    &__title {
        @extend .h4;
    }

    &__details {
        @extend .h6;
    }
}

.products__pic {
    padding-top: 76%;
    background-size: cover;
    background-repeat: no-repeat;

    &--28 {
        background-image: url('../images/product-big-28.png');
    }
}

.breadcumb{
    top: $space-unit * 6;
}