// -------------------------material
.material {

    &__list__item {

        &.flour {
            top: 20px;
            right: 40%;
        }

        &.ma {
            top: 10px;
            right: 5%;
            transform: rotate(-45deg);

            p {
                transform: rotate(45deg);
            }
        }

        &.azuki {
            top: 160px;
            right: 20%;
        }

        &.taro {
            bottom: 50px;
            right: 40%;
        }

        &.peanut {
            bottom: -15px;
            right: 0;
        }

        &__word {
            @include size(70px, 70px);
            border-radius: 50%;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            background-color: #fff;
            @extend .h1;
            text-align: center;
            color: $dark-gray;
        }
    }
}

.banner--material {
    .banner__card {
        &:after {
            height: 44px;
        }
    }

    &--azuki .container {
        background-image: url('../images/material-bg3.png');
        background-size: 110%;
    }
}