.navbar {
    background: linear-gradient(180deg, #F2F2F2 0%, #FFFDFA 14%, #FFFDFA 72%, $light-gray 100%);

    .container {
        height: 100px;
    }


}

.nav {
    display: flex;

    &__item ~ &__item {
        margin-left: 62px;
    }

    &__link {
        padding: 6px 12px;
        @extend .h5;
        letter-spacing: .05rem;
        background: $light-gray;

        &--active{
            background:$primary;
            color:#fff;
            font-weight: 500;
        }
    }
}