// -------------------------rank
.rank{

    .swiper__button {
        top: 50%;
        transform: translateY(-50%);

        &-prev {
            left: 0;
        }

        &-next {
            right: 0;
        }
    }

    .card__list__item {
        position: relative;
        transition: .2s;

        &__pic--index {
            position: absolute;
            top: -40px;
        }

        &:hover {
            transform: translateY(-10px);
        }
    }
}

