.logo {
    @include size(160px, 60px);
    background: url('../images/logo.svg') center / cover no-repeat;
    @include text-hide;

    // @include mobile-sm {
    //     @include size(48px, 29px);
    // }
}


.bottom-line {
    position: relative;

    &:after {
        content: '';
        position: absolute;
        width: 60px;
        border: 1px solid $black;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0 auto;
    }
}

.paper {
    background-color: $white;
    box-shadow: 6px 6px 10px #E2E2E2;
}

.label {
    width: 124px;
    line-height: 48px;
    text-align: center;
    background-color: $primary;
    color: $white;
    font-size: 2rem;
    font-weight: medium;
}

.carousel__indicators {
    display: flex;

    &__item {
        @include size(25px, 30px);
        margin-right: 2px;
        background-image: url('../images/azuki.svg');
        cursor: pointer;
        @include text-hide;

        &--active {
            background-image: url('../images/azuki-dark.svg');
        }
    }

}

.swiper__button {
    @include size(56px, 56px);
    font-size: 2.4rem;
    line-height: 56px;
    background-color: $white;
    color: $light-primary;
    text-align: center;
    box-shadow: 0px 3px 6px $light-gray;
}

.section__title {
    @extend .h2;
    text-align: center;
    @extend .bottom-line;
    @extend .mb-10;
}

.card__list {
    display: flex;
    justify-content: space-between;

    &__item {
        width: calc((100% - 84px) / 3);
        @extend .pb-9;
    }

    &__item__pic {
        display: flex;

        >* {
            margin: auto;
        }
    }

    &__item__title {
        @extend .h3;
        text-align: center;
        @extend .mb-3;
    }

    &__item__details {
        @extend .h5,
        .mb-3;
        text-align: center;
    }
}

.concept {
    .card__list__item__pic {
        height: 200px;
    }

    .card__list__item__details {
        text-align: left;
        @extend .px-7;
    }
}



