.d {
    &-block {
        display: block;
    }

    &-flex {
        display: flex;
    }
}

.po{
    &-re{
        position: relative;
    }
    &-ab{
        position: absolute;
    }
}

.block-center {
    margin: 0 auto;
}

.flex {
    &-wrap {
        flex-wrap: wrap;
    }
    &-reverse{
        flex-direction: row-reverse;
    }

    &-column {
        flex-direction: column;
    }

}

.justify-content {
    &-center {
        justify-content: center;
    }

    &-between {
        justify-content: space-between;
    }

    // &-around {
    //     justify-content: space-around;
    // }

    // &-end {
    //     justify-content: flex-end;
    // }
}

.align-items {
    &-center {
        align-items: center;
    }

    &-start {
        align-items: flex-start;
    }

    // &-end {
    //     align-items: flex-end;
    // }
}

.align-self {
    &-end {
        align-self: flex-end;
    }
}


