.banner {
    .container {
        height: 668px;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
    }

    &__card {
        position: relative;
        background-color: #fff;

        &:after {
            @include pattern(100%);
            bottom: 0;
            left: 0;
            height: 88px;
        }

        @extend .px-6;
    }

    &__card--right {
        position: absolute;
        right: $container-s-unit;
        bottom: 120px;
        @extend .py-8;
    }
}

// -------------------------paging
// -------------------------index
.banner--index {
    .container {
        background-image: url('../images/banner-bg.png');
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
    }

    .banner__bao{
        top : 6px;
        right: 35%;
    }

    .banner__hand {
        top: 20px;
        right: 38%;
        width: 945px;
        animation: slideout  2s 1s 1 ease-out forwards;
    }

    @media screen and (min-width: 1560px) {
        .banner__bao{
            right: 40%;
        }
    
        .banner__hand {
            top: 20px;
            right: 42%;
            animation: slideout2  2.5s 1s 1 ease-out forwards;
        }
        @keyframes slideout2 {
            0% {
                transform: scale(1);
            }
    
            20% {
                transform: scale(1.1);
            }
    
            25% {
                right: 42%;
                top: 20px;
                transform: scale(1.1);
        
            }
    
            100% {
                right: 90vw;
                top: 250px;
                transform: scale(1.1);
            }
        }  
    }
    @keyframes slideout {
        0% {
            transform: scale(1);
        }

        20% {
            transform: scale(1.1);
        }

        25% {
            right: 37%;
            top: 20px;
            transform: scale(1.1);
    
        }

        100% {
            right: 85vw;
            top: 120px;
            transform: scale(1.1);
        }
    }

    .banner__card {
        width: 20%;
    }
}

// -------------------------brand
.banner--brand{
    .container{
        background-image: url('../images/brand-banner.png');
        background-size: cover;
    }
}