// ----------------------------margin-top
@each $level in 25 {
  .mt-#{$level} {
      margin-top: $space-unit * $level;
  }
}
// ----------------------------margin-left
@each $level in 3,19,25 {
  .ml-#{$level} {
      margin-left: $space-unit * $level;
  }
}
@each $level in 8,9,10,15 {
  .mr-#{$level} {
      margin-right: $space-unit * $level;
  }
}
// ----------------------------padding top & bottom
@each $level in 10,15,25,30,55 {
  .pt-#{$level} {
      padding-top: $space-unit * $level;
  }
}
@each $level in 9,11,15,16,23,25{
  .pb-#{$level} {
    padding-bottom: $space-unit * $level;
  }
}

@each $level in 4{
  .pl-#{$level} {
    padding-left: $space-unit * $level;
  }
}

@each $level in 25{
  .pr-#{$level} {
    padding-right: $space-unit * $level;
  }
}

// ----------------------------padding x,y
@each $level in 6,7 {
  .px-#{$level}{
    padding-left: $space-unit * $level;
    padding-right: $space-unit * $level;
  }
}

@each $level in 6,8,15,16,20,25 {
  .py-#{$level}{
    padding-top: $space-unit * $level;
    padding-bottom: $space-unit * $level;
  }
}

.m-auto{
  margin: auto;
}