// color
$primary: #7a031f;
$light-primary: #A5354F;
$white: #ffffff;
$light-gray: #f2f2f2;
$gray: #666;
$dark-gray: #585858;
$black: #363434;


// font
$font-family-base: 'Noto Sans', sans-serif;
$font-size-base: 10px;
$line-height-base: 1.5;

// spacing
$space-unit : 4px;


// breakpoints
@mixin pad {//md
  @media (max-width: 992px) {
    @content;
  }
}

@mixin mobile-lg { 
  @media (max-width: 767px) {
    @content;
  }
}

@mixin mobile-sm {//sm
  @media (max-width: 576px) {
    @content;
  }
}

@mixin pc-1400 {//lg
  @media (min-width: 1400px) {
    @content;
  }
}

@mixin mobile-650 { 
  @media (max-width: 650px) {
    @content;
  }
}


@mixin mobile-414 { 
  @media (max-width: 414px) {
    @content;
  }
}


@mixin mobile-375 {
  @media (max-width: 375px) {
    @content;
  }
}